.home-wall {
  background-image: url(/img/walls/agra-fort-india-1600x1000.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
  height: 480px;
  height: 50vh;
  position: relative; }
  .home-wall .wall-title {
    color: #FFFFFF;
    position: absolute;
    right: 1rem;
    bottom: 1rem;
    font-size: 1.125rem; }

.packages-list {
  list-style: none;
  padding: 0;
  margin: 0; }
  .packages-list .list-item {
    margin: 1rem;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative; }

.packages-panel-heading {
  margin: 0 1rem; }

.package-link {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 0.5rem;
  display: block;
  color: #FFFFFF; }
  .package-link:hover {
    color: #FFFFFF; }

.package-heading {
  /*color: scale-color($color-primary-blue, $lightness: 0, $saturation: 20%);*/
  color: #FFCC2A;
  font-weight: 400;
  margin: 0; }

.upcoming-packages {
  margin-top: 1rem;
  /* At 768px - custom media query range */ }
  .upcoming-packages .list-item {
    height: 200px; }
  @media only screen and (min-width: 40.063em) {
    .upcoming-packages .list-item {
      float: left;
      height: 260px; }
    .upcoming-packages .two-items {
      width: calc(50% - 1rem - 1rem); } }
  @media only screen and (min-width: 48em) {
    .upcoming-packages .list-item {
      float: left;
      height: 320px; }
    .upcoming-packages .two-items {
      width: calc(50% - 1rem - 1rem); } }

.featured-packages {
  margin-top: 1rem;
  padding-bottom: 1rem;
  position: relative; }
  .featured-packages .featured-list-item {
    height: 220px; }
    .featured-packages .featured-list-item:before {
      position: absolute;
      display: block;
      width: 100%;
      height: 100%; }
  .featured-packages .explore-link {
    margin: 0 1rem;
    font-size: 1.125rem;
    /* 18px/16px */ }
  @media only screen and (min-width: 40.063em) {
    .featured-packages .featured-list-item {
      float: left;
      width: 30%;
      /* Fallback */
      width: calc(33.33% - 1rem - 1rem); }
    .featured-packages .explore-link {
      position: absolute;
      bottom: 0;
      right: 1rem;
      margin: 0; } }
  @media only screen and (min-width: 64.063em) {
    .featured-packages .featured-list-item.large {
      width: 40%;
      /* Fallback */
      width: calc(42% - 1rem - 1rem);
      height: 280px; }
    .featured-packages .featured-list-item.small {
      width: 22%;
      /* Fallback */
      width: calc(29% - 1rem - 1rem); }
    .featured-packages .explore-link {
      margin-bottom: 3rem;
      font-size: 1.3125rem;
      /* 21px / 16px */ } }
